/* eslint-disable */
import React, { Component } from 'react';
import Footer from './Footer';
import Loader from './Loader';
import myPicture from '../images/my-photo2.jpg';
import iconPhone from '../images/phone_svg.svg';
import iconMail from '../images/mail_svg.svg';
import iconContacts from '../images/contacts_svg.svg';


const JumboTronMini = ({
  title, main_p, second_p, third_p,
}) => (
  <div className="my_into_mini_jumbo">
    <h2>{title}</h2>
    <p>{main_p}</p>
    <p className="thin">{second_p}</p>
    {third_p && <p className="thin">{third_p}</p>}
  </div>
);


class Contacts extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
  }
  componentDidMount() {
    window.scrollTo(0, 0);
    this.setState({ loading: false });
  }
  render() {
    if (!this.state.loading) {
      return (
        <div className="home_pahe_contacts_container">
          <p className="background_omega_text">CONTACTS</p>
          <h1>Contacts</h1>
          <div className="flex_center icon_containter">
            <div><img alt="" src={iconMail} /></div>
            <div><img alt="" src={iconContacts} /></div>
            <div><img alt="" src={iconPhone} /></div>
          </div>
          <div className="contacts_wraper">
            <div className="my_into_mini_jumbo_container">
              <JumboTronMini
                title="Email"
                main_p="Email me at any time."
                second_p="georgidimitranov97@gmail.com"
                third_p="g_dimitranov@abv.bg"
              />
              <div className="my_into_mini_jumbo">
                <h2>Message</h2>
                <p>Add me on Facebook or Linkedin.</p>
                <p>
                  <a href="https://www.facebook.com/georgi.dimitranov">
                    <span>Facebook</span>
                  </a>
                </p>
                <p>
                  <a href="https://www.linkedin.com/in/georgi-dimitranov-059789150/">
                    <span>
                      Linkedin
                    </span>
                  </a>
                </p>
              </div>
              <JumboTronMini
                title="Call"
                main_p="Give me a call."
                second_p="+359 889 190 860"
              />
            </div>
          </div>
          <Footer />
        </div>
      );
    } return <Loader />;
  }
}

export default Contacts;



