import React from 'react';
import LayoutLogo from 'material-ui/svg-icons/hardware/developer-board';
import CodeLogo from 'material-ui/svg-icons/action/code';
import { Link } from 'react-router-dom';
import MyLogo from '../images/logo_light.svg';

const MyIntoduction = () => (
  <div className="introduction_container_all">
    <div className="introduction_logo_container">
      <img src={MyLogo} alt="" className="home_page_main_logo" />
    </div>
    <h1 className="main_title">
      Hello, my name is Georgi Dimitranov and im a future Front-End Developer.
    </h1>
    <h2 className="main_title_under">Explore my work and see how can I be useful to you.</h2>,
    <div className="flex_center introduction_button_container">
      <Link to="/projects">
        <button className="button_icon home_page_main_btn"><LayoutLogo />
          My Projects
        </button>
      </Link>
      <a href="https://github.com/dimitranov/myprojects">
        <button className="button_icon home_page_main_btn"><CodeLogo />
          My Repository
        </button>
      </a>
    </div>
    <svg viewBox="0 0 1280 110" preserveAspectRatio="none" className="header_polygon_shadow">
      <polygon points="0,110 1280,0 1280,110" />
    </svg>
    <svg viewBox="0 0 1280 110" preserveAspectRatio="none" className="header_polygon">
      <polygon
        points="0,110 1280,0 1280,110"
        style={{
          fill: '#F5F5F5',
        }}
      />
    </svg>
  </div>
);

export default MyIntoduction;



