/* eslint-disable */
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import LogoNameHOrizontalLight from '../images/logo_name_horizonal_light.svg';
import LogoNameHOrizontalDark from '../images/logo_name_horizontal_darck.svg';
import $ from 'jquery';

const changeIMG = () => {
  if (window.location.pathname === '/projects' || window.location.pathname === '/contacts') {
    return LogoNameHOrizontalDark;
  } return LogoNameHOrizontalLight;
};
const changeMenuClassName = () => {
  if (window.location.pathname === '/projects' || window.location.pathname === '/contacts') {
    return 'header_menu_links_container_dark';
  } return 'header_menu_links_container';
};
class HeaderMenu extends Component {
  componentDidMount() {
    window.scrollTo(0, 0);
  }
  render() {
    return (
      <div className="header_menu_container">
        <div className="header_menu_wraper">
          <div className="header_menu_img_container"><img src={changeIMG()} alt="" /></div>
          <div className={changeMenuClassName()}>
            <Link to="/"><span>Home</span></Link>
            <Link to="/projects"><span>Projects</span></Link>
            <Link to="/contacts"><span>Contacts</span></Link>
          </div>
        </div>
      </div>
    );
  }
}

export default HeaderMenu;



