/* eslint-disable */
import React from 'react';
import $ from "jquery";
import {  Link } from "react-router-dom";
import Phone from 'material-ui/svg-icons/hardware/phone-iphone';
import Tablet from 'material-ui/svg-icons/hardware/tablet-mac';
import Monitor from 'material-ui/svg-icons/hardware/desktop-mac';

const SingleProject = ({data}) => (
  <div className="home_page_projects_contant_holder">
    <div className="text_div">
      <h2>{data.title}
        <span>
          {data.isPhone && <Phone color="#50484e"/>}
          {data.isTablet && <Tablet color="#50484e"/>}
          {data.isDesctop && <Monitor color="#50484e"/>}
        </span>
      </h2>
      <p>{data.text}</p>
      {data.time && <p><span>Time:</span> {data.time}</p>}
      {data.learned && <p><span>Learned:</span> {data.learned}</p>}
      <div className="button_div">
        <div className="tech_holder">
          <img alt="" src={data.picture1}/>
          <img alt="" src={data.picture2}/>
          <img alt="" src={data.picture3}/>
          <img alt="" src={data.picture4}/>
          {data.picture5&&<img alt="" src={data.picture5}/>}
        </div>
        {data.hasCaseStudy && <Link to={`/projects/${data.title.toLowerCase().split(' ').join('-')}`}><button>Case Study</button></Link>}
        {window.innerWidth < 480 && <a href={data.url}>
          <button>Visit App</button>
        </a>}
        {data.hasViewCodeButton && <a href={data.codeFolderURL}>
          <button>View Code</button>
        </a>}
      </div>
    </div>
    <div className="img_div"
      onMouseEnter={() => {
        $(`.hidden_div_visit_app_button_${data.title.split(" ").join("")}`).addClass("hidden_div_visit_app_button_visible");
      }}
      onMouseLeave={() => {
        $(`.hidden_div_visit_app_button_${data.title.split(" ").join("")}`).removeClass("hidden_div_visit_app_button_visible");
      }}
      >
      {(data.noSite)?
        null:
        <div className={`hidden_div_visit_app_button hidden_div_visit_app_button_${data.title.split(" ").join("")}`}>
          <a href={data.url}><button>Visit App</button></a>
        </div>}
      <img alt="" src={data.pictureMain} />
    </div>
  </div>
)

export default SingleProject;



