import React, { Component } from 'react';
import ArrowDown from 'material-ui/svg-icons/navigation/arrow-downward';
import Footer from './Footer';
import MyIntoduction from './MyIntoduction';
import HomePageProjects from './HomePageProjects';
import MyMitovation from './MyMitovation';
import Toolset from './Toolset';
import InfoForMe from './InfoForMe';
import Loader from './Loader';

class HomePage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({ loading: false });
    }, 1000);
  }
  render() {
    if (!this.state.loading) {
      return (
        <div>
          <header className="home_page_header">
            <MyIntoduction />
          </header>
          <div className="flex_center home_arrow_downward_container">
            <ArrowDown className="home_arrow_downward" />
          </div>
          <InfoForMe />
          <hr />
          <MyMitovation />
          <hr />
          <HomePageProjects />
          <hr />
          <Toolset />
          <Footer />
        </div>

      );
    } return <Loader />;
  }
}


export default HomePage;



