/* eslint-disable */
import React, {Component} from 'react';
import $ from 'jquery';
import Footer from './Footer';
import PropTypes from 'prop-types';
import Loader from './Loader';
import Phone from 'material-ui/svg-icons/hardware/phone-iphone';
import Tablet from 'material-ui/svg-icons/hardware/tablet-mac';
import Monitor from 'material-ui/svg-icons/hardware/desktop-mac';
import desctopPhoto from '../images/movie-blob_desktop.png';
import mobilePhoto from '../images/movie-blob_mobile.png';
import logoPhoto from '../images/logos/react.png';
import react_logo from '../images/logos/react.png';
import redux_logo from '../images/logos/redux.png';
import firebase_logo from '../images/logos/firebase.png';
import css3 from '../images/logos/css3.png';
import html5 from '../images/logos/html5.png';
import jquery from '../images/logos/jquery.png';
import js from '../images/logos/js.png';
import es6 from '../images/logos/es6.png';
import ps from '../images/logos/ps.png';
import material from '../images/logos/material.png';

const ProjectDetails = (data) => (<div className="text_div">
  <h3>Details</h3>
  <p>
    <span>Resume:</span>
    {data.text}</p>
  {
    data.time && <p>
        <span>Time:</span>
        {data.time}</p>
  }
  {
    data.learned && <p>
        <span>Learned:</span>
        {data.learned}</p>
  }
  {
    data.practiced && <p>
        <span>Practiced:</span>
        {data.practiced}</p>
  }
  {
    data.responsive && <p>
        <span>Responsive:</span>
        {data.responsive.isPhone && <Phone color="#50484e"/>}
        {data.responsive.isTablet && <Tablet color="#50484e"/>}
        {data.responsive.isDesctop && <Monitor color="#50484e"/>}
      </p>
  }
</div>)

const ProjectUserTechs = (techs) => (<div>
  {
    techs.map((tech) => {
      switch (tech) {
        case "react_logo":
          return <img alt="" src={react_logo} key={tech}/>
          break;
        case "redux_logo":
          return <img alt="" src={redux_logo} key={tech}/>
          break;
        case "firebase_logo":
          return <img alt="" src={firebase_logo} key={tech}/>
          break;
        case "css3":
          return <img alt="" src={css3} key={tech}/>
          break;
        case "html5":
          return <img alt="" src={html5} key={tech}/>
          break;
        case "jquery":
          return <img alt="" src={jquery} key={tech}/>
          break;
        case "js":
          return <img alt="" src={js} key={tech}/>
          break;
        case "es6":
          return <img alt="" src={es6} key={tech}/>
          break;
        case "ps":
          return <img alt="" src={ps} key={tech}/>
          break;
        case "material":
          return <img alt="" src={material} key={tech}/>
          break;
        case "charts_js":
          return <img alt="" src={"http://www.chartjs.org/img/chartjs-logo.svg"} key={tech}/>
          break;
        default: return null;
      }
    })
  }
</div>)

const DesktopPicture = (projectName) => (<div className="img_des_container">
  <img src={`https://raw.githubusercontent.com/dimitranov/portfoliodata/master/${projectName}/${projectName}_desktop.png`}/>
</div>)

const MobilePicture = (projectName) => <img src={`https://raw.githubusercontent.com/dimitranov/portfoliodata/master/${projectName}/${projectName}_mobile.png`} className="img_mob"/>

const LogoPicture = (pic) => (<div className="p_logo_holder">
  <div>
    <img src={pic}/>
  </div>
</div>)


const RenderingCaseStudy = (projectDATA_ARRAY) => <div>
  {
    projectDATA_ARRAY.map((element, index) => {

      switch (element.type) {
        case "main_heading":
          return <h2 className="h2_big" key={index}>{element.value}</h2>
          break;
        case "sub_heading":
          return <h3  key={index}>{element.value}</h3>
          break;
        case "text":
          return <p  key={index}>{
              element.value.split(" ").map((word,index) => {
                if (word.substr(0, 1) == "%") {
                  return <span className="text_code_span" key={index}>{word.substr(1) + " "}
                  </span>
                } else
                  return word + " ";
                }
              )
            }</p>
          break;
        case "img":
          return <img src={element.value} alt=""  key={index}/>
          break;
        default:
          return null;
      }
    })
  }
</div>

class Project extends Component {
  constructor(props) {
    super(props);
    this.state ={
      data:null,
      loading:true,
      allLoaded:false,
    }
  }
   componentWillMount() {
    $.get(`https://raw.githubusercontent.com/dimitranov/portfoliodata/master/${this.props.match.params.projectName}/${this.props.match.params.projectName}_data.json`, (data) => {
      //console.log(JSON.parse(data));
      this.setState({
         data: JSON.parse(data),
         loading:false,
       })
      // this.data =  data.split(" ").join("");
      //console.log(typeof(data), data , data.split(" ").join(""));
      setTimeout(() => {
        this.setState({allLoaded:true});
      },200)
    });
   }
  componentDidMount() {
    window.scrollTo(0, 0);
     $(document).ready(() => {
       $(window).scrollTop(0);
       $(window).scroll(() => {
         let wScroll = $(window).scrollTop();
         $(".img_des_container img").css({'transform':'translate3d(0px, -'+ wScroll/4+'px, 0)'});
         $(".img_mob").css({'transform':'translate3d(0px, '+ wScroll/5+'px, 0)'});
       });
     });
  }
  render() {
    const { projectName } = this.props.match.params;
    const { data } = this.state;

    if(!this.state.loading){ return (<div className={this.state.allLoaded?"project_page_container project_page_container_visible":"project_page_container"}>
      <div className="project_content">
        {data.details.hasDesktopImg && DesktopPicture(projectName)}
        {data.details.hasMobileImg && MobilePicture(projectName)}
        <div className="project_data_container">
          {LogoPicture(logoPhoto)}
          <h1>{data.details.title}</h1>
          <div className="project_info_container flex_center">
            <div className="p_i_details">

              {ProjectDetails(data.details)}
            </div>
            <div className="p_i_techs">
              <h3>Used Technologies</h3>
              {ProjectUserTechs(data.details.techs)}
            </div>
          </div>
          <div className="flex_center case_study_link_buttons_container">
            <a href={data.details.url_link}><button>Visit Site</button></a>
            <a href={data.details.url_link_code}><button>View Code</button></a>
          </div>

          <div className="case_study_container">
            <h1>Case Study</h1>
            <div className="case_study_data_container">
              {RenderingCaseStudy(data.case_study)}
            </div>
          </div>
        </div>
        <Footer />
      </div>
    </div>);} else return <Loader/>;
  }
}


export default Project;



