import React from 'react';
import myPicture from '../images/myPicture.jpg';

const InfoForMe = () => (
  <div className="my_info_all_container">
    <p className="background_omega_text">WHOIAM</p>
    <h1 className="my_info_h1">Who I am</h1>
    <div className="flex_center">
      <div className="my_info_my_picture_contianer flex_center">
        <img alt="" src={myPicture} />
      </div>
    </div>
    <p className="my_info_p">
      My name is Georgi Dimitranov, in 20-year-old Software Technologies and Design Student who wants to become a professional Front - End Developer. I have a strong interest in Single Page Web Application  Development also knows as SPA with modern Front-End  JavaScript Frameworks and  Libraries like ReactJS, Angular 4 and Vue 2. At this point in learning and building application using ReactJS.
    </p>
    <div className="my_into_mini_jumbo_container">
      <div className="my_into_mini_jumbo">
        <h2>My goals today</h2>
        <p>My main goal today is to start as an intern or a junior in some company. I will devote my self to build a good career as a Front End Developer.</p>
      </div>
      <div className="my_into_mini_jumbo">
        <h2>Why ReactJS</h2>
        <p>The whole concept of building separate and reusable Web Components is the main reason I like working with React. Also, the API that the library provides is small but functional which leads to fast developing complex application.</p>
      </div>
      <div className="my_into_mini_jumbo">
        <h2>The Future</h2>
        <p>I look forward to expanding my skills in React and also Start Learning Frameworks like Angular 2/4 and Bootstrap 4. Sass and Less are on my list too.</p>
      </div>
    </div>
  </div>
);

export default InfoForMe;



