/* eslint-disable */
import React from 'react';
import logo from '../images/logo_light.svg';
import funCode from '../images/fun_code.png';
import $ from "jquery";
import {  Link } from "react-router-dom";

const Footer = (props) => (
  <footer>
      <div className="logo_holder">
        <img alt="" src={logo}/>
      </div>
      <div className="footer_navigation">
        <ul>
          <Link to="/"><li>Home</li></Link>
          <Link to="/projects"><li>Projects</li></Link>
          <Link to="/contacts"><li>Contacts</li></Link>
        </ul>
      </div>
      <div className="footer_navigation">
        <ul>
          <a href="https://github.com/dimitranov/myprojects"><li>Github Repository</li></a>
          <a href="https://www.facebook.com/georgi.dimitranov"><li>Facebook</li></a>
          <a href="https://www.linkedin.com/in/georgi-dimitranov-059789150/"><li>Linkedin</li></a>
        </ul>
      </div>
      <div className="fun_code_container">
        <img src={funCode} alt=""/>
      </div>
  </footer>
)

export default Footer;



