class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading:true,
      allLoaded:false,
    };
  }
  componentDidMount(){
      window.scroll(0,0);
      this.setState({loading:false})
      setTimeout(() => {
        this.setState({allLoaded:true})
      },200)
  }
  render() {
    if(!this.state.loading){
    return (
      <div className={this.state.allLoaded?"projects_page_container projects_page_container_visible":"projects_page_container"}>
        <div className="projects_page_h1_container">
          <h1>Projects</h1>
        </div>
        {
          data.map((unit) => {
            return(
              <div className="projects_single_project_holder" key={unit.title}>
                <SingleProject data={unit} />
              </div>
            )
          })
        }
        <Footer />
      </div>
    );} else return <Loader/>
  }

}

export default Projects;



