/* eslint-disable */
import React from 'react';
import { Route, BrowserRouter, Switch } from 'react-router-dom';
import HomePage from './components/HomePage';
import HeaderMenu from './components/HeaderMenu';
import Projects from './components/Projects';
import Project from './components/Project';
import Contacts from './components/Contacts';
import Footer from './components/Footer';
import './App.css';

const App = props => (
  <BrowserRouter>
    <div>
      <HeaderMenu />
      <Switch>
        <Route path={`${process.env.PUBLIC_URL}/`} exact component={HomePage} />
        <Route path={process.env.PUBLIC_URL+"/contacts"} exact component={Contacts}/>
        <Route path={process.env.PUBLIC_URL+"/projects"} exact component={Projects}/>
        <Route path={process.env.PUBLIC_URL+"/projects/:projectName"} exact component={Project}/>
      </Switch>

    </div>
  </BrowserRouter>
  )


export default App;



