
import React from 'react';
import { Link } from 'react-router-dom';
import LayoutLogo from 'material-ui/svg-icons/hardware/developer-board';

const MyProjectsSummary = () => (
  <div className="projects_summary_wraper">
    <h2>Personal Projects</h2>
    <p>My personal projects are all that  I have, as experience in Front End Development. Most of them are ReactJS projects but I also have a few file-base projects that use plain JS and jQuery script files. Mainly I create them to extend my knowledge, earn new skills and practice old ones.</p>

      <Link to="/projects">
        <button className="button_icon home_page_main_btn"><LayoutLogo />
          My Projects
        </button>
      </Link>
    
  </div>
);

const HomePageProjects = () => (
  <div className="home_page_projects_all_container">
    <h1 className="my_info_h1">Projects</h1>
    {/* <h3>Latest</h3> */}
    <div className="colageApps_container">
      {MyProjectsSummary()}
    </div>
  </div>);

export default HomePageProjects;



