import React from 'react';
import CodeLogo from 'material-ui/svg-icons/action/code';
import mainImg from '../images/floating_image_1.png';
import i1 from '../images/floating_image_2.png';
import i2 from '../images/floating_image_3.png';
import i3 from '../images/floating_image_4.png';
import i4 from '../images/floating_image_5.png';


const SVGelemtn = () => (
  <svg viewBox="0 0 1280 110" preserveAspectRatio="none" className="header_polygon">
    <polygon
      points="0,0 1280,50 1280,0"
      style={{
        fill: '#F5F5F5',
      }}
    />
  </svg>
);

const MyMitovation = () => (
  <div className="motivation_container_all">
    <p className="background_omega_text">MOTIVATION</p>
    <h1 className="my_info_h1">Motivation</h1>
    {/*SVGelemtn()*/}
    <div className="motivation_content_container flex_center">
      <div className="floating_images_container">
        <img className="main" alt="" src={mainImg} />
        <img className="i1" alt="" src={i1} />
        <img className="i2" alt="" src={i2} />
        <img className="i3" alt="" src={i3} />
        <img className="i4" alt="" src={i4} />
      </div>
      <div className="aside_text_wraper_container">
        <div className="aside_text_wraper ">
          <h2>Golas</h2>
          <p>My main goal and I can say it's even one of my dreams is becoming a Profesional Front End Developer. Most people at my age are so disoriented in the decisions they make in there life and they don't know what is their dream profession, I can proudly say I'm NOT one of them. I've always felt inspired when I go to a good looking and functioning website or application. When I think about the whole process of creating such practical and pretty designs inspire me to learn and practice more Front-end development in my personal projects.</p>
          <a href="https://github.com/dimitranov/myprojects" style={{float:"Right"}}>
            <button className="button_icon"><CodeLogo color="white"/>
              My Repository
            </button>
          </a>
        </div>
      </div>
    </div>
  </div>
);
/*
As I sed I have my goals and reasons. And what better to have on your team, than one full of energy, ready to learn and smart young man.My main goal and I can say its even one of my dreams is becoming a Profesional Front-end developer. Most people at my age are so disoriented in the decisions they make in there life and they don't know what is their dream profession, I can proudly say I'm NOT one of them.I've always felt inspired when I go to a good looking and functioning website or application. When I think about the whole process of creating such practical and pretty designs inspire me to learn and practice more Front-end development in my personal projects.
*/
export default MyMitovation;



